<template>
    <div>
        <h1>{{ msg }}</h1>
        <div id="example-1">
            <h3></h3>
            <p>
                <code>v-on</code>DOMJavaScript</p>
            <button v-on:click="counter += 1"> 1</button>
            <p> {{ counter }} </p>
        </div>

        <hr>

        <div id="example-2">
            <h3></h3>
            <p>JavaScript
                <code>v-on</code>
                <code>v-on</code></p>
            <!-- 'greet'  -->
            <button v-on:click="greet">Greet</button>
        </div>

        <hr>

        <div id="example-3">
            <h3></h3>
            <p>JavaScript</p>
            <button v-on:click="say('hi')">Say hi</button>
            <button v-on:click="say('what')">Say what</button>
        </div>

        <div id="example-4">
            <p>DOM
                <code>$event</code></p>
            <button v-on:click="warn('Form cannot be submitted yet.', $event)">Submit</button>
        </div>

        <hr>

        <div id="example-5">
            <h3></h3>
            <p>
                <code>event.preventDefault()</code>
                <code>event.stopPropagation()</code>methodsmethodsDOM</p>
            <p>Vue.js
                <code>v-on</code>(.)</p>
            <ul>
                <li>.stop</li>
                <li>.prevent</li>
                <li>.capture</li>
                <li>.self</li>
                <li>.once</li>
            </ul>
        </div>

        <hr>

        <div id="example-6">
            <h3></h3>
            <ul>
                <li>.enter</li>
                <li>.tab</li>
                <li>.delete</li>
                <li>.esc</li>
                <li>.space</li>
                <li>.up</li>
                <li>.down</li>
                <li>.left</li>
                <li>.right</li>
            </ul>
            <form>
                <input type="text" v-on:keyup="submit">
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name: 'v-on',
    data () {
        return {
            msg: 'v-on',
            counter: 0,
            name: 'Vuejs'
        }
    },
    methods: {
        greet (event) {
            // 'this'Vue
            alert('Hello ' + this.name + '!')
            // 'event'DOM
            alert(event.target.tagName)
        },
        say (message) {
            alert(message)
        },
        warn (message, event) {
            if (event) event.preventDefault()
            alert(message)
        },
        submit (event) {
            console.log(event.key)
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}
</style>



