<template>
    <div class="element">
        <h2></h2>
        <h3>Layout </h3>
        <p>24</p>
        <h4></h4>
        <p></p>
        <div>
            <el-row :gutter="20">
                <el-col :span="24">
                    24
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">12</el-col>
                <el-col :span="12">12</el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :span="8" :offset="2">
                    :gutter="24" 
                </el-col>
                <el-col :span="8" :offset="2">
                    :offset="2" 
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="8" :offset="2">
                    :gutter="24" 
                </el-col>
                <el-col :span="8" :offset="2">
                    :offset="2" 
                </el-col>
            </el-row>
            <el-row>
                <h3></h3>
                <el-col :span="12">
                     el-icon-iconName 
                </el-col>
                <el-col :span="12">
                    <i class="el-icon-edit"></i>
                    <i class="el-icon-share"></i>
                    <i class="el-icon-delete"></i>
                    <el-button type="primary" icon="el-icon-search"></el-button>
                </el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :span="8">
                    <el-button></el-button>
                    <el-button type="primary"></el-button>
                    <el-button type="text"></el-button>
                </el-col>
                <el-col :span="8">
                    <el-button :plain="true" :disabled="true"></el-button>
                    <el-button type="primary" :disabled="true"></el-button>
                    <el-button type="text" :disabled="true"></el-button>
                </el-col>
                <el-col :span="8">
                    <el-button type="success"></el-button>
                    <el-button type="warning"></el-button>
                    <el-button type="danger"></el-button>
                    <el-button type="info"></el-button>

                    <el-button :plain="true" type="success"></el-button>
                    <el-button :plain="true" type="warning"></el-button>
                    <el-button :plain="true" type="danger"></el-button>
                    <el-button :plain="true" type="info"></el-button>
                </el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :span="6">
                    <el-button type="primary" icon="el-icon-edit"></el-button>
                    <el-button type="primary" icon="el-icon-share"></el-button>
                    <el-button type="primary" icon="el-icon-delete"></el-button>
                    <el-button type="primary" icon="el-icon-search"></el-button>
                    <el-button type="primary">
                        <i class="el-icon-upload el-icon-right"></i>
                    </el-button>
                </el-col>
                <el-col :span="6">
                    <el-button-group>
                        <el-button type="primary" icon="el-icon-arrow-left"></el-button>
                        <el-button type="primary">
                            <i class="el-icon-arrow-right el-icon--right"></i>
                        </el-button>
                    </el-button-group>
                    <el-button-group>
                        <el-button type="primary" icon="el-icon-edit"></el-button>
                        <el-button type="primary" icon="el-icon-share"></el-button>
                        <el-button type="primary" icon="el-icon-delete"></el-button>
                    </el-button-group>
                </el-col>
                <el-col :span="6">
                    :loading="true" : 
                    <br>
                    <el-button type="primary" :loading="true"></el-button>
                </el-col>
                <el-col :span="6">
                    size: large | small | mini
                    <br>
                    <el-button type="primary" size="large"></el-button>
                    <el-button type="primary"></el-button>
                    <el-button type="primary" size="small"></el-button>
                    <el-button type="primary" size="mini"></el-button>
                </el-col>
            </el-row>
        </div>

        <h4>Form</h4>
        <div>
            <h3></h3>
            <el-row>
                <el-col :span="12">
                    <el-radio v-model="radio" label="1"></el-radio>
                    <el-radio v-model="radio" label="2"></el-radio>
                    <el-radio v-model="radio" label="3"></el-radio>
                    <el-radio v-model="radio" label="4"></el-radio>
                </el-col>
                <el-col :span="12">
                    <el-radio disabled v-model="radio1" label="1"></el-radio>
                    <el-radio disabled v-model="radio1" label="2"></el-radio>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-radio-group v-model="radio2">
                        <el-radio :label="3"></el-radio>
                        <el-radio :label="6"></el-radio>
                        <el-radio :label="9"></el-radio>
                    </el-radio-group>
                </el-col>
                <el-col :span="12">
                    <el-radio-group v-model="radio3">
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                    </el-radio-group>
                    <br>
                    <el-radio-group v-model="radio4">
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label="" :disabled="true"></el-radio-button>
                    </el-radio-group>
                    <br>
                    <el-radio-group v-model="radio5" :disabled="true">
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label=""></el-radio-button>
                    </el-radio-group>
                </el-col>
            </el-row>
            <br>
            <h3></h3>
            <el-row :gutter="24">
                <el-col :span="8">
                    <el-checkbox v-model="checked"></el-checkbox>
                </el-col>
                <el-col :span="8">
                    <el-checkbox v-model="checked1" disabled></el-checkbox>
                    <el-checkbox v-model="checked2" disabled></el-checkbox>
                </el-col>
                <el-col :span="8">
                    <el-checkbox-group v-model="checkList">
                        <el-checkbox label="Stefan"></el-checkbox>
                        <el-checkbox label="Katherine"></el-checkbox>
                        <el-checkbox label="Elena"></el-checkbox>
                        <el-checkbox label="Damon"></el-checkbox>
                        <el-checkbox label="" disabled></el-checkbox>
                        <el-checkbox label="" disabled></el-checkbox>
                    </el-checkbox-group>
                </el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :span="8">
                    <el-checkbox v-model="checkAll" :indeterminate="isIndeterminate" @change="handleCheckAllChange"></el-checkbox>
                    <div style="margin: 15px 0;"></div>
                    <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                        <el-checkbox v-for="city in cities" :label="city" :key="city">{{ city }}</el-checkbox>
                    </el-checkbox-group>
                </el-col>
                <el-col :span="8">
                    <el-checkbox-group v-model="checkedCities1" :min="1" :max="2">
                        <el-checkbox v-for="city in cities" :label="city" :key="city">
                            {{ city }}
                        </el-checkbox>
                    </el-checkbox-group>
                </el-col>
                <el-col :span="8">
                    <el-checkbox-group v-model="checkboxGroup1">
                        <el-checkbox-button v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox-button>
                    </el-checkbox-group>
                    <div style="margin: 15px 0;"></div>
                    <el-checkbox-group v-model="checkboxGroup2" size="small">
                        <el-checkbox-button v-for="city in cities" :label="city" :disabled="city === ''" :key="city">{{city}}</el-checkbox-button>
                    </el-checkbox-group>
                    <div style="margin: 15px 0;"></div>
                    <el-checkbox-group v-model="checkboxGroup3" size="large" fill="#324057" text-color="#a4aebd" :min="1" :max="3">
                        <el-checkbox-button v-for="city in cities" :label="city" :key="city">{{city}}</el-checkbox-button>
                    </el-checkbox-group>
                </el-col>
            </el-row>
            <h3></h3>
            <el-row :gutter="24">
                <el-col :span="8">
                    <el-input v-model="input" placeholder="" suffix-icon="el-icon-date"></el-input>
                    <el-input v-model="input" placeholder="" prefix-icon="el-icon-search"></el-input>

                    <el-input placeholder="" v-model="input">
                        <i slot="suffix" class="el-input__icon el-icon-date"></i>
                    </el-input>
                    <el-input placeholder="" v-model="input">
                        <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                </el-col>
                <el-col :span="8">
                    <el-input type="textarea" v-model="input" :rows="3" placeholder=""></el-input>
                </el-col>
                <el-col :span="8">
                    <el-input placeholder="" v-model="input">
                        <template slot="prepend">Http://</template>
                        <template slot="append">.com</template>
                    </el-input>
                </el-col>
            </el-row>
            <el-row :gutter="24">
                <el-col :span="24">
                    <el-input placeholder="" v-model="input" class="input-with-select">
                        <el-select v-model="select" slot="prepend" placeholder="">
                            <el-option label="" value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                            <el-option label="" value="3"></el-option>
                        </el-select>
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>
        </div>
        <h4>Data</h4>
        <h4>Notice</h4>
        <h4>Navigation</h4>
        <h4>Others</h4>
    </div>
</template>

<script>
let cityOptions = ['', '', '', '']
export default {
    name: 'element',
    data () {
        let msg = 'Element'
        return {
            msg: msg,
            radio: '2',
            radio1: '1',
            radio2: 3,
            radio3: '',
            radio4: '',
            radio5: '',
            checked: true,
            checked1: false,
            checked2: true,
            checkList: ['', 'Katherine'],
            checkAll: true,
            checkedCities: ['', ''],
            cities: cityOptions,
            isIndeterminate: true,
            checkedCities1: ['', ''],
            checkboxGroup1: [''],
            checkboxGroup2: [''],
            checkboxGroup3: [''],
            input: '',
            select: ''
        }
    },
    methods: {
        handleCheckAllChange (event) {
            this.checkedCities = event.target.checked ? cityOptions : []
            this.isIndeterminate = false
        },
        handleCheckedCitiesChange (value) {
            let checkedCount = value.length
            this.checkAll = checkedCount === this.cities.length
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
        }
    }
}

</script>

<style>
</style>



