<template>
    <div>
        <!-- prop -->
        <!-- v-bind -->
        <!-- v-bind true 123 -->
        <blog-post v-bind="post" prop-f="success">

        </blog-post>
    </div>
</template>
<script>
// 
// props
// data(prop)
// computed()
// : String, Number, Boolean, Object, Array, Date, Function, Symbol
// : Person, Dog, Girl ...
let Child = {
    props: {  // props
        post: {
            type: Object,
            required: false
        },
        title: {
            type: String,
            required: true
        },
        content: {
            type: [String, Number],
            required: false
        },
        propA: Boolean,
        propB: [String, Number],
        propC: {
            type: Object,
            required: false
        },
        propD: {
            type: Number,
            default: 100
        },
        propE: {
            type: Object,
            default () {
                return { message: 'hello' }
            }
        },
        propF: {
            type: String,
            validator: function (value) {
                // 
                return ['success', 'warning', 'danger'].indexOf(value) !== -1
            }
        }
    },
    data () {
        return {
            article: {
                title: '',
                content: '',
                time: '2018-08-07'
            }
        }
    },
    template: `
        <div>
            <h3>{{ title }}</h3>
            <p>{{ content }}</p>
            <p>
                prop-f: {{ propF }}
            </p>
        </div>
    `
}

export default {
    data () {
        return {
            post: {
                title: '',
                content: '',
                time: '2018-08-07'
            }
        }
    },
    components: {
        'blog-post': Child
    },
    created () {
        console.log(this.axios)
    }
}
</script>
<style scoped>
</style>



