class Animal {
    constructor () {
        this.type = 'animal'
    }

    say (str) {
        // console.log('${this.type}say:${str}');
        setTimeout(() => {
            console.log(this.type + ' says:' + str)
        }, 1000)
    }
}

export default class extends Animal {
    constructor () {
        super()
        this.type = 'dog'
    }
}

