// es6
class Point {
    constructor (x, y) {
        this.x = x
        this.y = y
    }

    toString () {
        return '(' + this.x + ', ' + this.y + ')'
    }
}

var p = new Point(1, 2)
console.log(p.toString())

// 2 Class

class ColorPoint extends Point {
    constructor (x, y, color) {
        super(x, y)
        this.color = color
    }

    toString () {
        return this.color + ' ' + super.toString()
    }
}

var p2 = new ColorPoint(1, 2, 'red')
console.log(p2.toString())
