                    <h3 class="animated jello">{{ film.name }}</h3>
                    <span>{{ film.title }}</span>
                    <el-tag>{{ film.type }}</el-tag>
                    <el-button type="primary" icon="delete" @click="deleteFilm(film)"></el-button>
                </li>
            </ul>
            <div>
                <div>
                    <h3>3</h3>
                    <p>{{ film.name }}</p>
                    <p>{{ film.title }}</p>
                    <p>{{ film.hot }}</p>
                    <p>{{ film.type }}</p>
