        deleteRow (index, rows) {
            rows.splice(index, 1)
            this.$message({
                message: '',
                type: 'success'
            })
        },
        open2 (index, rows) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                rows.splice(index, 1)
                this.$message({
                    message: '',
                    type: 'success'
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })
