<template>
    <div class="introduce">
        <div>
            <h3>Vue.js</h3>
            <p>Vue.jsVueVueVueVue</p>
        </div>
        <div>
            <h3></h3>
            <p>
                {{ msg }}
                <br>
                <span v-bind:title="message"></span>
            </p>
        </div>
        <div>
            <h3></h3>
            <p v-if="seen"></p>
            <br>
            <ol>
                <li v-for="(todo, index) in todos" :key="index">
                    {{ todo.text }}
                </li>
            </ol>
        </div>
        <div>
            <h3></h3>
            <p>{{ msg }}</p>
            <button v-on:click="reverseMessage"></button>
        </div>
        <div>
            <p>Vue
                <code>v-model</code></p>
            <p>{{ msg }}</p>
            <input type="text" v-model="msg">
        </div>
        <hr>
        <div v-html="html">

        </div>
        {{ num + 1 }} {{ status ? 'success' : 'fail' }}
        <div>
            <a v-bind:href="link" v-bind:title="msg"></a>
            <p v-for="(item, index) in list" :key="index">
                {{ item.name }} - {{ item.age }} - {{ index }}
            </p>
        </div>
        <div>
            <p v-for="(value, key) in obj" :key="key">
                {{ key + ': ' + value }}
            </p>
        </div>
        <div>
            <p v-if="status">A</p>
            <p v-else>B</p>
            <button v-on:click="toggle">toggle</button>
        </div>
        <div>
            <p></p>
            <banner @my-event="onComaMyEvent" number=4>
                <p slot="header">header</p>
                <p slot="footer">footer</p>
            </banner>
        </div>
        <div>
            <input type="text" v-model="text">
            <p>{{ text }}</p>
        </div>
        <div>
            <p></p>
            {{ myValueWithoutNum }}
        </div>

    </div>
</template>

<script>
import banner from '../../components/banner'
export default {
    components: {
        banner
    },
    name: 'introduce',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            message: ' ' + new Date(),
            seen: true,
            todos: [
                {
                    text: ' JavaScript'
                },
                {
                    text: ' Vue'
                },
                {
                    text: ''
                }
            ],
            text: '',
            html: '<ul><li></li><li></li><li></li></ul>',
            num: 1,
            status: true,
            link: 'https://www.baidu.com',
            list: [
                {
                    name: '',
                    age: 15
                },
                {
                    name: '',
                    age: 15
                }
            ],
            obj: {
                name: '',
                age: 15
            },
            user: []
        }
    },
    methods: {
        reverseMessage () {
            this.msg = this.msg.split('').reverse().join('')
        },
        toggle: function () {
            this.status = !this.status
        },
        onComaMyEvent (parmfromA) {
            console.log('onComaMyEvent ' + parmfromA)
        }
    },
    computed: {
        myValueWithoutNum: function () {
            return this.text.replace(/\d/g, '')
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul,
ol {
    list-style-type: none;
    padding: 0;
}

li {
    /*display: inline-block;*/
    margin: 0 10px;
}

a {
    color: #42b983;
}
</style>



